<template>
  <div class="update-gender">
       <van-picker
        show-toolbar
        title=""
        :default-index="value"
        :columns="columns"
        @cancel="$emit('close')"
        @confirm="onConfirm"
        @change="onChange"
        />
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user'
export default {
    name: 'updateGender',
    data() {
        return {
            columns: ['',''], // default-index  01
            genderIndex: 0,
            value: 0
        }
    },
    props: {
        sex: {
            type: String,
            required: true,
        }
    },
    created() {
        if(this.sex == '') {
            this.value = 0
        }else {
            this.value = 1
        }
    },
    methods: {
        onChange(data, value, index) {
            // console.log(index)
            this.genderIndex = index
        },
        async onConfirm() {
            console.log('');
            //  
            // name
            this.$toast.loading({
                message:'',
                forbidClick: true // 
            })
            // try
            await updateUserProfile({
                sex: this.genderIndex+1
            })
            this.$emit('close') // 
            if(this.genderIndex==0) {
                this.genderIndex = ''
            } else {
                this.genderIndex = ''
            }
            this.$emit('update-sex', this.genderIndex) // 
            this.$toast.success({
                message:'',
                forbidClick: true // 
            })
            
        }
    },
    // props: {
    //    value: {
    //        type: Number,
    //        required: true
    //    } 
    // }
}
</script>

